// Colors
// для нейминга цветов https://www.htmlcsscolor.com/hex/334482

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

$color-burnt-orange:                #ff812e;
$color-persimmon:                   #ea5d00;
$color-romantic:                    #fdc6a2;
$color-whisper:                     #e9e9e9;
$color-sauvignon:                   #eae7e5;
$color-grey:                        #828282;
$color-nero:                        #222222;
$color-zambezi:                     #595959;
$color-zircon:                      #dde9ea;
$color-white-smoke:                 #f5f5f5;

$color-overlay:                     rgba(0, 0, 0, 0.25);

// Fonts

$fz-default:                        13px;

$ff-montserrat:                     "Montserrat", "Helvetica", sans-serif;

// Animation

$trans-300:                         300ms;
$trans-800:                         800ms;
$trans-1000:                        1000ms;
$trans-2000:                        2000ms;

// Viewports

$vp-375:                            375px;
$vp-768:                            768px;
$vp-1200:                           1200px;
$vp-1440:                           1440px;

// Retina

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;

$custom-cursor:                     url('../img/cursor.png'), auto;
